<template>
  <div >
    <div class="cor-fundo fundo-mobile">
      
      <div>
        <h1 style="color:white">Experimente agora mesmo. É fácil!</h1>
      </div>
      <b-button
        variant="success"
        lg="4"
        class="px-5 side2"
        style="font-weight:bold;"
        :href="`https://api.whatsapp.com/send?phone=5561982573269&text=Olá gostaria de implantar a o pdvende no meu empreendimento.`"
                  target="_blank"
                  >Experimentar Grátis <b-img src="/imgsubmenu/whatsapp.svg" height="30px"></b-img></b-button
          ><b-container>
      <div class="mb-7">
        <h6 style="color:white">
          Ao baixar e instalar o Programa PDVende você concorda com os Termos de
          <a
             href="../pdfitens/PolíticaPrivacidade.pdf"
                  target="_blank"
            style="color:white"
          >
            Serviço do Software
            <b-icon-download class="ml-2"></b-icon-download>
          </a>
        </h6>
      </div>
      </b-container>
    </div>

    <b-modal
      size="lg"
      id="modal-prevent-closing"
      ref="modal"
      @show="resetModal"
      @hidden="resetModal"
      @ok="handleOk"
      :hide-footer="true"
      class="mobile-item"
    >
      <div class="modal-titulo" style="font-family:Poppins">PDVENDE</div>
      <div class="progress">
        <div
          class="progress-bar progress-bar-striped progress-bar-animated"
          role="progressbar"
          aria-valuenow="75"
          aria-valuemin="0"
          aria-valuemax="100"
          style="width: 75%"
        >80% Completo</div>
      </div>

      <p
        class="my-4 text-center"
        style="font-family:Poppins"
      >Falta Pouco! Você está a um passo de conhecer uma ferramenta incrível.</p>
      <b-row>
        <b-col cols="3" class="mobile-item">
          <b-img src="./imagens/dowload-modal.svg" style="width: 160px; height: 160px;"></b-img>
        </b-col>
        <b-col>
          <form ref="form" @submit.stop.prevent="handleSubmit">
            <b-form-group
              :state="nameState"
              label-for="name-input"
              invalid-feedback="Email incorreto! Por favor Tente novamente!"
            >
              <h3 style="font-family:Poppins">Digite seu e-mail para começar grátis</h3>
              <b-form-input
                id="name-input"
                type="email"
                v-model="name"
                :state="nameState"
                invalid-feedback
                required
              ></b-form-input>
              <div></div>
            </b-form-group>
            <b-button variant="success" @click="handleOk" class="btn-side botao-mobile">Experimentar Grátis</b-button>
            <div style="font-size:12px">
              <b-form-checkbox :checked="true">
                Aceito receber e-mails de informações do PDVende.               
                <a
                  href="../pdfitens/PolíticaPrivacidade.pdf"
                  target="_blank"
                >Política de privacidade.</a>
              </b-form-checkbox>
            </div>
          </form>
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>
<script>
import config from "../../config";
import axios from "axios";
import Swal from "sweetalert2";

export default {
  data() {
    return {
      config: config,
      name: "",
      nameState: null,
      submittedNames: []
    };
  },
  methods: {
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();
      this.nameState = valid;
      if (valid) {
        this.salvarEmail();
      }
      return valid;
    },
    resetModal() {
      this.name = "";
      this.nameState = null;
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing

      bvModalEvt.preventDefault();
      // Trigger submit handler
      this.handleSubmit();
    },
    handleSubmit() {
      // Exit when the form isn't valid
      if (!this.checkFormValidity()) {
        return;
      }
      // Push the name to submitted names
      this.submittedNames.push(this.name);
      // Hide the modal manually
      this.$nextTick(() => {
             this.$bvModal.hide("modal-prevent-closing");
      });
    },
    async salvarEmail() {
      try {
      let timerInterval;
      Swal.fire({
        title: "Por favor Aguarde...",
        timer: 2000000,
        timerProgressBar: false,
        onBeforeOpen: () => {
          Swal.showLoading();
          timerInterval = setInterval(() => {
            const content = Swal.getContent();
            if (content) {
              const b = content.querySelector("b");
              if (b) {
                b.textContent = Swal.getTimerLeft();
              }
            }
          }, 100);
        },
        onClose: () => {
          clearInterval(timerInterval);
        },
      }).then((result) => {
        /* Read more about handling dismissals below */
        if (result.dismiss === Swal.DismissReason.timer) {
          console.log("I was closed by the timer");
        }
      });
        var data ={
          email:this.name
        }
       var response = await axios.post(
          `${config.API_URL}/EmailCliente/EnviarEmailCliente`,
          data
        );
        if (response.status == 200 || response.status == 204) {
        //   window.open(
        //     "https://arquivopdvende.file.core.windows.net/pdvende/PDVende.exe?sv=2019-10-10&ss=bqtf&srt=sco&sp=rwdlacup&se=2020-07-17T02:18:01Z&sig=Fql4AzIYATD%2BGzp0BITNkWBSAGApNuqK4W%2Fvz8ySslc%3D&_=1594924472181"
        //   );
         Swal.fire({
          position: "center",
          icon: "success",
          title: "Nossa equipe entrará em contato em breve. Obrigado por escolher a PDVende!",
          showConfirmButton: true,
          timer: 15000,
        });
        }
        
        
       
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>


