  border-color: #28a745;
  width: 100%;
  font-size: 12px;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 20px 30px;
  font-size: 25px;
}

.cor-fundo {
  color: white !important;
  padding: calc(113px + -5rem) 0rem 0;
  margin-top: -41px;
  overflow: hidden;
  background: #4823d7;
}
.modal-titulo {
  font-weight: bold;
  font-size: 22px;
  line-height: 100%;
  color: #385a7b;
  margin-bottom: 1.5em;
  text-align: center;
}

.side2 {
  color: white;
  background-color: #28a745;
  border-color: #28a745;
  font-size: 12px;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 20px 30px;
  font-size: 20px;
}

.modal-header {
  display: none !important;
