<script>
import config from "../../config";
export default {
  data() {
    return {
      config: config,
    };
  },
  methods: {
    adiquirirPlano(numeroplano) {
      if (numeroplano == 1) {
        this.$router.push({
          name: "Checkout",
          params: { idPlano: config.BASICO, valor: config.CODIGO_PLANO_BASICO },
        });

        return;
      } else if (numeroplano == 2) {
        this.$router.push({
          name: "Checkout",
          params: {
            idPlano: config.FISCALNFE,
            valor: config.CODIGO_PLANO_FISCAL_FISCALNFE,
          },
        });
        return;
      } else if (numeroplano == 3) {
        
        this.$router.push({
          name: "Checkout",
          params: {
            idPlano: config.FISCALNFCE,
            valor: config.CODIGO_PLANO_FISCAL_FISCALNFCE,
          },
        });
        return;
      } else if (numeroplano == 4) {
        this.$router.push({
          name: "Checkout",
          params: {
            idPlano: config.COMPLETO,
            valor: config.CODIGO_PLANO_FISCAL_COMPLETO,
          },
        });
        return;
      }
    },
  },
};
</script>
